.App {
  text-align: center;
  position: relative;
  background-image: linear-gradient(#304ffe, white);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.overlay {
  position: absolute;
  top: 660px;
  right: 155px;
  bottom: 0;
  left: 155px;
  color: #fff;
  text-align: center;
  align-content: left;
}

.inARow {
  display: inline-block;
  margin: 5px;
}

